.home-container {
   min-height: 100vh;
   width: 100%;
   display: flex;
  }
  .left-container{
   background: black;
    width: 70%;
    padding-left: 3rem;
  }
  .right-container{
background: linear-gradient(-45deg, #FF69B4, black 70%);
width: 30%;

  }
  .left-container,.right-container{
    height: 100vh;
    display: flex;
    align-items: center;
  }
  .content{
    margin-left: 4rem;
  }
  .content .intro{
    font-size: 1.5rem;
    color: white;
    animation: fadeIn 2s ease-in-out infinite alternate;
  
   

    
  }
  .content .intro span{
    color: #FF69B4;
  }
 .content h1{
  font-size: 4rem;
  color: #FF69B4;
  animation: slideIn 2s ease-in-out infinite alternate;
 
 }
 .content .detail{
  font-size: 1.1rem;
  color: white;
  max-width: 38rem;
  animation: fadeInUp 2s ease-in-out infinite alternate;
 
  
  
 }
 .icons {
  color: #FF69B4 ;
  font-size: 40px;
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
 justify-content: center;
 align-items: center;
 }
 .design .circle{
aspect-ratio: 1;
position: absolute;
border-radius: 50%;
top: 50%;
left: 70%;
transform: translate(-50%,  -50%);

 }
 .circle:nth-child(1){
 background: linear-gradient(90deg, transparent 50%, black 50%);
  width: 30rem;
 }
 .circle:nth-child(2){
  background-image: url("C:\Users\Mohini Pandey\OneDrive\Desktop\portflio\myportflio\public\images\mypic.jpg");
  width: 25rem;
  background-size: 100%;
  background-repeat: no-repeat;
  border: 2px solid white;
  box-shadow: 
  0 0 1rem white,
  inset 0 0 1rem white,
  0 0 2rem #FF69B4,
  inset 0 0 2rem #FF69B4,
  0 0 4rem #FF69B4                 ;
 }
 @keyframes fadeIn {
  from {
      opacity: 0;
  }
  to {
      opacity: 1;
  }
}
@keyframes slideIn {
  from {
      transform: translateY(-50px);
      opacity: 0;
  }
  to {
      transform: translateY(0);
      opacity: 1;
  }
}
@keyframes fadeInUp {
  from {
      opacity: 0;
      transform: translateY(20px);
  }
  to {
      opacity: 1;
      transform: translateY(0);
  }
} 




